import { Component, OnInit } from '@angular/core';
declare const google;
import { ComponentFactoryResolver ,ViewChild, ElementRef} from '@angular/core';
import { Geolocation } from '@ionic-native/geolocation/ngx';
import { Subscription, observable } from 'rxjs';
import { filter } from 'rxjs/operators';
@Component({
  selector: 'app-skytricker-next',
  templateUrl: './skytricker-next.page.html',
  styleUrls: ['./skytricker-next.page.scss'],
})

export class SkytrickerNextPage implements OnInit {
  currentColor:any;
  @ViewChild('map') mapElement: ElementRef;
  map: any;
  trackedRoute = [];
  time:any;
  speed:any;
  distance:any;
  currentMapTrack = null;
  positionSubscription: Subscription;
  constructor(public geolocation: Geolocation) { 
    this.currentColor = 'light';

        this.distance=localStorage.getItem('distance')
        this.speed=localStorage.getItem('Average Speed')
        this.time=localStorage.getItem('TotalTime')
        this.trackedRoute = JSON.parse(localStorage.getItem('trackedRoute'));

        
   
  }

  ngOnInit() {
    this.startMapVal()
  }

  redrawPath(path) {
    if (this.currentMapTrack) {
      this.currentMapTrack.setMap(null);
    }
  
    if (path.length > 1) {
      this.currentMapTrack = new google.maps.Polyline({
        path: path,
        geodesic: true,
        strokeColor: '#f58c29',
        strokeOpacity: 1.0,
        strokeWeight: 7
      });
      this.currentMapTrack.setMap(this.map);
    }
  
  }


  startMapVal() {
    let posMaceio = { lat: -9.616139, lng: -35.817239 }
    let map = new google.maps.Map(this.mapElement.nativeElement, {
      zoom: 18,
      center: posMaceio,
      mapTypeId: 'roadmap'
    });

    let infoWindow = new google.maps.InfoWindow({ map: map });

    // Try HTML5 geolocation.
    if (navigator.geolocation) {

      navigator.geolocation.getCurrentPosition((position) => {
        let pos = {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        };
        console.log(pos)
        //start position
        

        let marker = new google.maps.Marker({
          position: pos,
          map: map
        });
        map.setCenter(pos);
        this.map = map;

        this.redrawPath(this.trackedRoute)
        this. draw()


      }, () => {
        this.handleLocationError(true, infoWindow, map.getCenter());
      });
    } else {
      // Browser doesn't support Geolocation
      this.handleLocationError(false, infoWindow, map.getCenter());
    }




  }


  draw(){
 
  
  
  
    this.positionSubscription = this.geolocation.watchPosition()
    .pipe(
      filter((p) => p.coords !== undefined) //Filter Out Errors
    )
    .subscribe(data => {
      setTimeout(() => {
        this.redrawPath(this.trackedRoute);
       
      }, 3000);
    });
  
  }

  handleLocationError(browserHasGeolocation, infoWindow, pos) {
    infoWindow.setPosition(pos);
    infoWindow.setContent(browserHasGeolocation ?
      'Error: The Geolocation service failed.' :
      'Error: Your browser doesn\'t support geolocation.');
  }

}
